.App {
  display: flex;
  flex-direction: column;
  text-align: center;
  justify-content: center;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
}

.form-content {
  display: flex;
  flex-direction: column;
  margin: 20px;
  text-align: center;
  align-items: center;
}
